import { css } from "@emotion/css";
import React from "react";

const style = css({
  ":hover": {
    path: {
      stroke: "white",
    },
    circle: {
      stroke: "white",
    },
    rect: {
      stroke: "white",
    },
    ".upper-content": {
      stroke: "unset",
      fill: "white",
    },
  },
});
const Devices = () => {
  return (
    <svg
      className={style}
      width="24"
      height="23"
      viewBox="0 0 10 9"
      fill="none"
      xmlns="http://www.w3.org/2000/svg"
    >
      <path
        d="M4.5 6H1.66667C1.29848 6 1 5.6802 1 5.28571V1.71429C1 1.3198 1.29848 1 1.66667 1H4.5"
        stroke="#888888"
        strokeWidth="0.5"
      />
      <rect
        x="5.25"
        y="0.25"
        width="4.5"
        height="8.5"
        rx="0.75"
        stroke="#888888"
        strokeWidth="0.5"
      />
      <circle cx="7.5" cy="6.5" r="1.25" stroke="#888888" strokeWidth="0.5" />
      <path className="upper-content" d="M7 1H8V3H7V1Z" fill="#888888" />
      <path d="M4.5 7H3" stroke="#888888" strokeWidth="0.5" />
    </svg>
  );
};

export default Devices;
